import React from 'react';
import {Tabs, Tab, Table, Col, Row, ProgressBar, Pagination} from 'react-bootstrap';
import listdata from '../List/ListData';



function PageTable(){	
	return(
		<div>
			<h4> Rumor Transfer Pemain</h4>
			<br/>
			<Tabs defaultActiveKey="profile" id="uncontrolled-tab-example">
  			<Tab eventKey="semua_transfer" title="Semua Transfer" disabled>    		
  			</Tab>
  			<Tab eventKey="liga_primer_inggris" title="Liga Primer Inggris">
    		</Tab>
  			<Tab eventKey="seri_a" title="Seri A" >    		
  			</Tab>
  			<Tab eventKey="divisi_primera" title="Divisi Primera" >
  			</Tab>
  			<Tab eventKey="bundes_liga" title="Bundes Liga" >    		
  			</Tab>
  			<Tab eventKey="ligaindonesia" title="Liga 1 Indonesia" >    		
  			</Tab>
			</Tabs>
			<br/>
			<Row>
      <Col>
      	<Table size="sm">
    			<thead>
      				<tr>
        				<th>#</th>
				        <th>Nama Pemain</th>
				        <th>Tim</th>
				        <th>Transfer</th>
				        <th>Proses Transfer</th>        
				     </tr>
    			</thead>
    			<tbody>
    				{listdata.map((data,i)=>{
    					return(
    					<tr key={i}>
    					<td>{1+i}</td>
        				<td>{data.nama}</td>
        				<td>{data.tim}</td>
        				<td>{data.transfer}</td>
        				<td><ProgressBar now={data.proses} label={`${data.proses}%`} />        	
        				</td>
        				</tr>
    					)
    				})      				
    				}
    			</tbody>
    		</Table>
        </Col>
  			</Row>

		<Pagination>
			<Pagination.First />
			  <Pagination.Prev />
			  <Pagination.Item active>{1}</Pagination.Item>
			  <Pagination.Ellipsis />

			  <Pagination.Item>{10}</Pagination.Item>
			  <Pagination.Item>{11}</Pagination.Item>
			  <Pagination.Item>{12}</Pagination.Item>
			  <Pagination.Item>{13}</Pagination.Item>
			  <Pagination.Item disabled>{14}</Pagination.Item>

			  <Pagination.Ellipsis />
			  <Pagination.Item>{20}</Pagination.Item>
			  <Pagination.Next />
			  <Pagination.Last />
		</Pagination>		
		</div>
		)
}

export default PageTable;